<template>
    <iframe v-if="this.video" :src="videoUrl" 
        width="800" 
        height="450" 
    />
</template>

<script>
import { videoBaseUrl } from "@/db.json"

export default {
    props: {
        video: {
            type: Object,
            required: true
        }
    },
    computed: {
        videoUrl: () => videoBaseUrl.replace('999999999', this.video.id)        
    },
}
</script>