<template>
  <table style="width: 100%">
    <tr><td>
      <div style="max-height: 600px; overflow-y: auto; text-align: left">
        <div v-for="(course, iCourse) in courses" :key="iCourse">
          <h2>{{ course.title }}</h2>
          <div class="lesson" v-for="(lesson, iLesson) in courseLessons(course.id)" :key="iLesson">
            <a @click="openLesson(iLesson)" target="_blank">
              {{ lesson.title }}
            </a>
          </div>
        </div>
      </div>
    </td><td style="min-width: 800px">
      <VideoShow :video="selectedLesson" />
    </td></tr>
  </table>  
</template>

<script>
import VideoShow from "@/components/VideoShow.vue"
import { courses, lessons } from "@/db.json"

export default {
  name: "VideosList",
  components: {
    VideoShow
  },
  props: {
    lessonIndex: {
      default: null
    }
  },
  created() {
    if (this.lessonIndex)
      this.openLesson(this.lessonIndex)
  },
  data: () => ({
    courses, lessons, selectedLesson: null
  }),
  methods: {
    openLesson: () => (iLesson) => { 
      this.selectedLesson = (iLesson === null) ? null : this.lessons[iLesson] 
    },
  },
  computed: {
    courseLessons: () => (idCourse) => this.lessons.filter(l => l.idCourse == idCourse),
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .lesson {
    padding: 4px;
    border: 1px solid gray;
    cursor: pointer
  }
  .lesson a {
    display: block
  }
</style>
